<template>
    <div CLASS='PICTURE-VIEW'>
            <dynamic-loader :data="data.data" />        
    </div>
</template>
<script>
import dynamicLoader from './v.s.loader.component'
export default {
    name:'PICTURE-VIEW',
    components:{
        dynamicLoader
    },
    props:['data'],
    data()
    {
        return {
            show:{
                is:'page',
                data:{
                is: 'card',
                data: {
                   is: 'box?columns=5em auto 0.5em&&rows=0.5em auto 0.5em',
                   
                    left:{
                        is:'textvertical',
                        text:'SuNnY lEoNe'
                    },
                    center: {
                        is: 'boxcenter',
                        center:{
                            is: 'video?showControls=true',
                            url:'https://video.redwap-cdn.com/key=nCs1NKM7jrMOIRXEFXDGtA,end=1553343873/289330_video.mp4'
                        }
                    }
                }   
                }
            }
        }
    }
}
</script>
<style lang="less">
.PICTURE-VIEW > .PAGE{
        --card-height:auto;
        --card-width:auto;
        justify-content: center !important;
        align-content: center !important;
        height: 100% !important;
        width:100% !important; 
        #center
        { 
            min-height: 9em !important;
            height: auto;
            width: auto;
            max-height: 90vh;
            max-width: 90vw;
        }
}
</style>



